import React from "react";
import Layout from '../../components/Global/Layout/Layout'
import Header from '../../components/Global/App/Header/Header.component'
import Map from '../../components/Local/Home/HomeTwo/Map/Map.HomeTwo.component'
import FeatureProperties from '../../components/Local/Home/HomeTwo/FeatureProperties/FeatureProperties.component'
import PropertyCities2 from '../../components/Local/Home/HomeTwo/PropertyCities/PropertiesCities.HomeTwo.component'
import BestProperty from '../../components/Local/Home/HomeTwo/BestProperty/BestProperty.HomeTwo.component'
import WhyChooseUs from '../../components/Global/WhyChooseUs/WhyChooseUs.component'
import OurBlogComponent from '../../components/Global/Home/OurBlog/OurBlog.component'
import OurPartners from '../../components/Global/OurPartners/OurPartners.component'
import StartPartners from '../../components/Global/StartPartners/StartPartners.component'
import Footer from '../../components/Global/App/Footer/Footer.component'
import AdvanceSearchOption from '../../components/Global/App/AdvanceSearchOption/AdvanceSearchOption.component'

const Home = () => {
	return (
		<Layout>
		 	<Header addClass="home2"/> 
			<Map />				
			<FeatureProperties Title="Latest For Sale">
				<div className="row featured_row">
					<div className="col-lg-12">
					<AdvanceSearchOption ClassName='home2'/>
					</div>
				</div>
			</FeatureProperties>
			<FeatureProperties Title="Latest For Rent"></FeatureProperties>
			<WhyChooseUs />
			<PropertyCities2 />	
			<BestProperty/>
			<OurBlogComponent />
			<OurPartners />
			<StartPartners addClass="bgc-thm"/>
			<Footer />
		</Layout>
	);
}

export default Home